$white: #fff;
$black: #232329;
$purple: #acadcc;
$grey: #d0d0d0;
$red: #ff3a1f;

/* roboto-regular - latin-ext_latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-v29-latin-ext_latin/roboto-v29-latin-ext_latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("../fonts/roboto-v29-latin-ext_latin/roboto-v29-latin-ext_latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("../fonts/roboto-v29-latin-ext_latin/roboto-v29-latin-ext_latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/roboto-v29-latin-ext_latin/roboto-v29-latin-ext_latin-regular.woff")
      format("woff"),
    /* Modern Browsers */
      url("../fonts/roboto-v29-latin-ext_latin/roboto-v29-latin-ext_latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/roboto-v29-latin-ext_latin/roboto-v29-latin-ext_latin-regular.svg#Roboto")
      format("svg"); /* Legacy iOS */
}
